.add-task-container {
  height: 100%;
  width: 100%;
}
.add-task-form {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1em 0 1em 0;
}

.box {
  width: 80%;
}

.form-title {
  margin: 0.5em;
  font-size: x-large;
  text-align: left;
}

.task-detail-container {
  display: flex;
  justify-content: left;
  width: 90%;
  flex-direction: column;
}

.links-container li {
  margin-top: 1em;
}

.links-container ul {
  list-style-type: none
}

.link {
  text-decoration: underline;
  color: black;
  margin-top: 1em;
}

.estimated-time-box {
  height: 100%;
  width: 25%;
}

.estimated-time-container {
  height: 20%;
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: x-large;
}

.standard-container-title {
  display: flex;
  justify-content: center;
  align-items: center;
}

.standard-container-title-icon {
  cursor: pointer;
  position: absolute;
  right: 0;
  margin-right: 1em;
}

.standard-container-title-icon:hover {
  transform: scale(1.1);
}

.task-description {
  margin-top: 2em;
  margin-bottom: 2em;
}
